import React, {useEffect} from "react";
import {makeStyles} from "@material-ui/core/styles";
import {Box, Grid} from "@material-ui/core";
import {getSchemas, getSchemaTypes, getUser} from "../../api";
import {useUser} from "../../providers/user/hooks";
import {useSchemas} from "../../providers/schemas/hooks";
import SearchField from "../SearchField";
import User from "../User";
import {withRouter} from "react-router-dom";
import {MAIN, SCHEMA} from "../../routes";
import GoBackButton from "../../pages/Schema/components/GoBackButton";

const useStyles = makeStyles((theme) => {
  return ({
    root: {
      display: "flex",
      justifyContent: "center",
      backgroundColor: theme.palette.primary.main,
      minHeight: "100vh",
      minWidth: "100%",
      padding: "32px 15px",
      [theme.breakpoints.down('md')]: {
        padding: '32px 0px',
      },
    },
    container: {
      width: "100%",
      maxWidth: 1120,
    },
    inner: {
      marginBottom: "40px",
      minHeight: "75px",
    }
  })
});

const AppContainer = ({children, location}) => {
  const classes = useStyles();
  const {setSchemas, selectedSchema, setSchemasTypes} = useSchemas();
  const {setUser} = useUser();

  useEffect(() => {
    Promise.all([
      getSchemas(),
      getSchemaTypes(),
      getUser()
    ]).then(([schemas, types, user]) => {
        setSchemas(schemas.data);
        setSchemasTypes(types.data);
        setUser(user.data)
      }
    )
  }, []);

  return (
    <Box component={"main"} className={classes.root}>
      <Box className={classes.container}>
        <Grid container>
          <Grid item xs={12}>
            <Grid container spacing={3} className={classes.inner}>
              <Grid item xs={6} md={8}>
                {location.pathname === MAIN && (
                  <SearchField/>
                )}
                {location.pathname.includes(SCHEMA) && selectedSchema.name && (
                  <GoBackButton to={MAIN} name={selectedSchema.name}/>
                )}
              </Grid>
              <Grid item xs={6} md={4}>
                <User/>
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={12}>
            {children}
          </Grid>
        </Grid>
      </Box>
    </Box>
  )
};

export default withRouter(AppContainer);