import {get} from "lodash";

export const initialState = {
  data: [
    {
      "id": 1,
      "name": "Kapitalforhøyelse",
      "attributes":
        {
          "BANK_OG_FINANS": "Bank Og Finans",
          "ENTERPRISE": "Enterprise"
        },
    }
  ],
  filterValue: "ALLE_SKJEMAER",
  searchValue: "",
  types: {
    ALLE_SKJEMAER: "Alle Skjemaer",
    "ARBEIDSRETT": "Arbeidsrett",
    "BANK_OG_FINANS": "Bank Og Finans",
    "ENTERPRISE": "Enterprise",
    "FORNYBAR_ENERGI": "Fornybar Energi",
    "FORSIKRING": "Forsikring",
  },
  selectedSchema: {}
};

export const reducer = (state, action) => {
  switch (action.type) {
    case 'SET_FILTER_VALUE':
      return {
        ...state,
        filterValue: action.value,
      };
    case 'SET_SCHEMAS':
      const result = get(action, "data", []).map(value => {
        return ({
          ...value,
          attributes: value.attributes.reduce((obj, item) => Object.assign(obj, item), {})
        })
      })
      return {
        ...state,
        data: [
          ...state.data,
          ...result
        ]
      };
    case 'SET_SCHEMAS_TYPES':
      const types = get(action, "data", []).reduce((obj, item) => Object.assign(obj, item), {});
      return {
        ...state,
        types: {
          ...state.types,
          ...types
        }
      };
    case 'SET_SEARCH_VALUE':
      return {
        ...state,
        searchValue: action.value,
      };
    case 'SET_SELECTED_SCHEMA':
      return {
        ...state,
        selectedSchema: state.data.find(item => String(item.id) === String(action.selectedSchema)) || {},
      };
    default:
      throw new Error();
  }
}