import {roles} from "../../../../models/user";
import React from "react";
import Select from "../../../../components/Select";
import {Box, Checkbox as MaterialCheckbox, TextField as MaterialTextField} from "@material-ui/core";

export const EditableCell = ({value: initialValue, row: {index}, column, updateMyData}) => {

  const [value, setValue] = React.useState(initialValue);

  const onChange = e => {
    setValue(e.target.value);
  }

  const onBlur = e => {
    updateMyData(index, column.id, value)
  }

  const onChangeCheckbox = e => {
    setValue(e.target.checked)
  }

  React.useEffect(() => {
    setValue(initialValue)
  }, [initialValue]);

  switch (column.id) {
    case "rolle":
      return <Select value={value} options={roles} onChange={onChange} onBlur={onBlur} />
    case "skalSignere":
      return (
        <Box>
          <MaterialCheckbox checked={value} onChange={onChangeCheckbox} onBlur={onBlur} />
          {value ? "Påkrevd" : "Ikke obligatorisk"}
        </Box>
      )
    default:
      return <MaterialTextField value={value} onChange={onChange} onBlur={onBlur}/>
  }
}