import React from 'react';
import {Box, Grid, IconButton} from "@material-ui/core";
import {Field} from "react-final-form";
import AddIcon from "@material-ui/icons/Add";
import ReactTable from "../../../../components/ReactTable";
import User from "../../../../components/Icons/User";
import FormSection from "../../components/FormSection";
import Checkbox from "../../components/Inputs/Checkbox";
import FinalFormSelect from "../../components/Inputs/Select";
import TextField from "../../components/Inputs/TextField";
import TextArea from "../../components/Inputs/TextArea";
import {columns} from "./columns";
import {useStyles} from "./styles";
import {EditableCell} from "./cell";

const Participants = ({onClick, data, setTableValues}) => {

  const classes = useStyles();

  return (
    <FormSection title={"Legg til Deltakere"}>
      <Grid container spacing={3}>
        <Grid item xs={4}>
          <Field
            name="navn"
            component={TextField}
            type="text"
            label="Navn"
          />
        </Grid>
        <Grid item xs={4}>
          <Field
            name="rolle"
            component={FinalFormSelect}
            type="select"
            label="Rolle"
          />
        </Grid>
        <Grid item xs={4}>
          <Box className={classes.subSubmit}>
            <Field
              name="skalSignere"
              component={Checkbox}
              type="checkbox"
              label="Skal signere"
            />
            <IconButton className={classes.subSubmitBtn} onClick={onClick}>
              <AddIcon/>
            </IconButton>
          </Box>
        </Grid>
        <Grid item xs={12}>
          <ReactTable
            data={data}
            columns={[
              ...columns,
              {
                Header: 'Action',
                accessor: 'action',
                style: {
                  width: "10%"
                },
                Cell: ({row, data}) => {
                  return (
                    <div onClick={() => {
                      const filtered = data.filter((val, index) => index !== row.index)
                      setTableValues(filtered)
                    }}>
                      <User/>
                    </div>
                  )
                }
              }
            ]}
            defaultColumn={{
              Cell: EditableCell,
            }}
            setTableValues={setTableValues}
          />
        </Grid>
        <Grid item xs={12}>
          <Field
            name="bakgrunn_for_kapitalforhøyelsen"
            component={TextArea}
            type="textarea"
            label="Bakgrunn for Kapitalforhøyelsen"
            placeholder="Begynn å skrive"
          />
        </Grid>
        <Grid item xs={12}>
          <Field
            name="beslutninger"
            component={TextArea}
            type="textarea"
            label="Beslutninger"
            placeholder="Alle beslutninger var enstemmige"
          />
        </Grid>
      </Grid>
    </FormSection>
  );
}

export default Participants;